@import url(https://fonts.googleapis.com/css?family=Amatic+SC);
/* -------------------------  Reset ------------------------- */
html {
    box-sizing: border-box;
    /* font-family: sans-serif; */
    font-family: 'Jost', sans-serif;
    font-weight: 400;
    font-size: 16px;
    scroll-behavior: smooth;
}

*,
*::after,
*::before {
    box-sizing: inherit;
}

body {
    margin: 0;
    overflow-x: hidden;
    /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif; */
    font-family: 'Jost', sans-serif;
    font-weight: 400;
}

a {
    text-decoration: none;
    transition: all 0.5s ease-out;
}

a:hover {
    opacity: 0.7;
}

h1 {
    margin: 0;
    font-size: 2rem;
}

h2 {
    margin: 0;
    font-size: 1.5rem;
}

h3 {
    margin: 0;
    font-size: 1.25rem;
}

h4 {
    margin: 0;
    font-size: 1rem;
}

h5 {
    margin: 0;
    font-size: 0.85rem;
}

h6 {
    margin: 0;
    font-size: 0.7rem;
}

img {
    max-width: 100%;
    height: auto;
}

p {
    line-height: 1.6;
}

/* ------------------------- Site Styles ------------------------- */
body {
    /* background: url("./assets/spices.jpg") no-repeat center center fixed; */
    background-color: #fff;
    background-size: cover;
}

a {
    color: #000;
}

a:visited {
    color: #000;
}

.container {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.fix {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: space-between;
}


@media (max-width: 1200px) {
    .container {
        padding: 0 1rem
    }
}



/* ------------------------- Loader ------------------------- */

.loader {
    background-color: rgba(0, 0, 0, 0.85);
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.loader h1 {
    position: relative;
    margin: 0 auto;
    top: 25vh;
    width: 100vw;
    text-align: center;
    font-family: "Amatic SC";
    font-size: 6vh;
    color: #f4952f;
    opacity: 0.75;
    -webkit-animation: pulse 2.5s linear infinite;
            animation: pulse 2.5s linear infinite;
}

#cooking {
    position: relative;
    margin: 0 auto;
    top: 0;
    width: 75vh;
    height: 75vh;
    overflow: hidden;
}

#cooking .bubble {
    position: absolute;
    border-radius: 100%;
    box-shadow: 0 0 0.25vh #f4952f;
    opacity: 0;
}

#cooking .bubble:nth-child(1) {
    margin-top: 2.5vh;
    left: 58%;
    width: 2.5vh;
    height: 2.5vh;
    background-color: #f4952f;
    -webkit-animation: bubble 2s cubic-bezier(0.53, 0.16, 0.39, 0.96) infinite;
            animation: bubble 2s cubic-bezier(0.53, 0.16, 0.39, 0.96) infinite;
}

#cooking .bubble:nth-child(2) {
    margin-top: 3vh;
    left: 52%;
    width: 2vh;
    height: 2vh;
    background-color: #f4952f;
    -webkit-animation: bubble 2s ease-in-out 0.35s infinite;
            animation: bubble 2s ease-in-out 0.35s infinite;
}

#cooking .bubble:nth-child(3) {
    margin-top: 1.8vh;
    left: 50%;
    width: 1.5vh;
    height: 1.5vh;
    background-color: #f4952f;
    -webkit-animation: bubble 1.5s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.55s infinite;
            animation: bubble 1.5s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.55s infinite;
}

#cooking .bubble:nth-child(4) {
    margin-top: 2.7vh;
    left: 56%;
    width: 1.2vh;
    height: 1.2vh;
    background-color: #f4952f;
    -webkit-animation: bubble 1.8s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.9s infinite;
            animation: bubble 1.8s cubic-bezier(0.53, 0.16, 0.39, 0.96) 0.9s infinite;
}

#cooking .bubble:nth-child(5) {
    margin-top: 2.7vh;
    left: 63%;
    width: 1.1vh;
    height: 1.1vh;
    background-color: #f4952f;
    -webkit-animation: bubble 1.6s ease-in-out 1s infinite;
            animation: bubble 1.6s ease-in-out 1s infinite;
}

#cooking #area {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 50%;
    height: 50%;
    background-color: transparent;
    -webkit-transform-origin: 15% 60%;
            transform-origin: 15% 60%;
    -webkit-animation: flip 2.1s ease-in-out infinite;
            animation: flip 2.1s ease-in-out infinite;
}

#cooking #area #sides {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: 15% 60%;
            transform-origin: 15% 60%;
    -webkit-animation: switchSide 2.1s ease-in-out infinite;
            animation: switchSide 2.1s ease-in-out infinite;
}

#cooking #area #sides #handle {
    position: absolute;
    bottom: 18%;
    right: 80%;
    width: 35%;
    height: 20%;
    background-color: transparent;
    border-top: 1vh solid #f4952f;
    border-left: 1vh solid transparent;
    border-radius: 100%;
    -webkit-transform: rotate(20deg) rotateX(0deg) scale(1.3, 0.9);
            transform: rotate(20deg) rotateX(0deg) scale(1.3, 0.9);
}

#cooking #area #sides #pan {
    position: absolute;
    bottom: 20%;
    right: 30%;
    width: 50%;
    height: 8%;
    background-color: #f4952f;
    border-radius: 0 0 1.4em 1.4em;
    -webkit-transform-origin: -15% 0;
            transform-origin: -15% 0;
}

#cooking #area #pancake {
    position: absolute;
    top: 24%;
    width: 100%;
    height: 100%;
    -webkit-transform: rotateX(85deg);
            transform: rotateX(85deg);
    -webkit-animation: jump 2.1s ease-in-out infinite;
            animation: jump 2.1s ease-in-out infinite;
}

#cooking #area #pancake #pastry {
    position: absolute;
    bottom: 26%;
    right: 37%;
    width: 40%;
    height: 45%;
    background-color: #f4952f;
    box-shadow: 0 0 3px 0 #f4952f;
    border-radius: 100%;
    -webkit-transform-origin: -20% 0;
            transform-origin: -20% 0;
    -webkit-animation: fly 2.1s ease-in-out infinite;
            animation: fly 2.1s ease-in-out infinite;
}

@-webkit-keyframes jump {
    0% {
        top: 24%;
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }

    25% {
        top: 10%;
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    50% {
        top: 30%;
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }

    75% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    100% {
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }
}

@keyframes jump {
    0% {
        top: 24%;
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }

    25% {
        top: 10%;
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    50% {
        top: 30%;
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }

    75% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }

    100% {
        -webkit-transform: rotateX(85deg);
                transform: rotateX(85deg);
    }
}

@-webkit-keyframes flip {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    5% {
        -webkit-transform: rotate(-27deg);
                transform: rotate(-27deg);
    }

    30%,
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    55% {
        -webkit-transform: rotate(27deg);
                transform: rotate(27deg);
    }

    83.3% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes flip {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    5% {
        -webkit-transform: rotate(-27deg);
                transform: rotate(-27deg);
    }

    30%,
    50% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    55% {
        -webkit-transform: rotate(27deg);
                transform: rotate(27deg);
    }

    83.3% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@-webkit-keyframes switchSide {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }

    50% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }

    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

@keyframes switchSide {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }

    50% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }

    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

@-webkit-keyframes fly {
    0% {
        bottom: 26%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    10% {
        bottom: 40%;
    }

    50% {
        bottom: 26%;
        -webkit-transform: rotate(-190deg);
                transform: rotate(-190deg);
    }

    80% {
        bottom: 40%;
    }

    100% {
        bottom: 26%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes fly {
    0% {
        bottom: 26%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    10% {
        bottom: 40%;
    }

    50% {
        bottom: 26%;
        -webkit-transform: rotate(-190deg);
                transform: rotate(-190deg);
    }

    80% {
        bottom: 40%;
    }

    100% {
        bottom: 26%;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@-webkit-keyframes bubble {
    0% {
        -webkit-transform: scale(0.15, 0.15);
                transform: scale(0.15, 0.15);
        top: 80%;
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(1.1, 1.1);
                transform: scale(1.1, 1.1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(0.33, 0.33);
                transform: scale(0.33, 0.33);
        top: 60%;
        opacity: 0;
    }
}

@keyframes bubble {
    0% {
        -webkit-transform: scale(0.15, 0.15);
                transform: scale(0.15, 0.15);
        top: 80%;
        opacity: 0;
    }

    50% {
        -webkit-transform: scale(1.1, 1.1);
                transform: scale(1.1, 1.1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(0.33, 0.33);
                transform: scale(0.33, 0.33);
        top: 60%;
        opacity: 0;
    }
}

@-webkit-keyframes pulse {
    0% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.25;
    }

    50% {
        -webkit-transform: scale(1.2, 1);
                transform: scale(1.2, 1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.25;
    }
}

@keyframes pulse {
    0% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.25;
    }

    50% {
        -webkit-transform: scale(1.2, 1);
                transform: scale(1.2, 1);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
        opacity: 0.25;
    }
}
